import { HttpErrorResponse } from '@angular/common/http';

export class AccountAccessDenied {
  messages: string[];
  kind: 'AccountAccessDenied' = 'AccountAccessDenied';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountBadRequest {
  messages: string[];
  kind: 'ProductKindsBadRequest' = 'ProductKindsBadRequest';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccounttNotFound {
  messages: string[];
  kind: 'ProductKindstNotFound' = 'ProductKindstNotFound';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountConnectivityError {
  messages: string[];
  kind: 'ProductKindsConnectivityError' = 'ProductKindsConnectivityError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class AccountUnknownError {
  messages: string[];
  kind: 'ProductKindsUnknownError' = 'ProductKindsUnknownError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = [response.message];
  }
}

export type AccountError =
  | AccountAccessDenied
  | AccountBadRequest
  | AccounttNotFound
  | AccountConnectivityError
  | AccountUnknownError;

export const parseError = (error: HttpErrorResponse): AccountError => {
  if (error.error instanceof ErrorEvent) {
    return new AccountUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new AccountConnectivityError(error);
      case 400:
        return new AccountBadRequest(error);
      case 401:
        return new AccountAccessDenied(error);
      case 403:
        return new AccountAccessDenied(error);
      case 404:
        return new AccounttNotFound(error);
      default:
        return new AccountUnknownError(error);
    }
  }
};
