import { Component, Directive, OnInit, OnDestroy, Inject, ViewChild, TemplateRef } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Observable, combineLatest, Subscription, BehaviorSubject, of } from 'rxjs';
import { catchError, filter, map, pluck, switchMap, tap } from 'rxjs/operators';
import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  processErrorMessages,
  ProductKindNames,
  WINDOW,
  LocalWindow,
  UnmaskedProductEventPayload,
  setAttribute,
  ProductMaskableAttribute,
  ProductsExternalInfoService,
  hasExternalInfo,
} from '@backbase/product-summary-common-ang';
import { AccountService } from '../account.service';
import {
  AccountArrangementItem as ProductType,
  MaskableAttribute,
  UnmaskedAttributeRequestParams,
} from '@backbase/data-ang/arrangements';
import { AccountError } from '../account-error';
import { NotificationService } from '@backbase/ui-ang/notification';

/**
 * Displays details for a product using its ID.
 * The ID is taken from the `selectedAccount` route param.
 *
 * The following extension slots are available in this component:
 *
 * - `ProductSummaryDetailsCustomizableDirective`
 *
 * @see ProductSummaryDetailsCustomizableDirective
 *
 * - `ProductSummaryDetailsHeaderCustomizableDirective`
 *
 * @see ProductSummaryDetailsHeaderCustomizableDirective
 *
 * - `ProductSummaryDetailsViewCustomizableDirective`
 *
 * @see ProductSummaryDetailsViewCustomizableDirective
 *
 * - `ProductSummaryDetailsLoadingCustomizableDirective`
 *
 * @see ProductSummaryDetailsLoadingCustomizableDirective
 *
 * - `ProductSummaryDetailsNoAccountSelectedCustomizableDirective`
 *
 * @see ProductSummaryDetailsNoAccountSelectedCustomizableDirective
 *
 * @usageNotes
 *
 * ### Display details of an account
 *
 * ```html
 * <bb-product-summary-account-details></bb-product-summary-account-details>
 * ```
 *
 * @ngModule ProductSummaryDetailsWidgetModule
 */
@Component({
  selector: 'bb-product-summary-details',
  templateUrl: './product-summary-details.component.html',
})
export class ProductSummaryDetailsComponent implements OnInit, OnDestroy {
  /**
   * Error notification template reference
   */
  @ViewChild('errorNotificationTemplate', { static: true }) errorNotificationTemplate: TemplateRef<any> | string = '';
  /**
   * @internal
   */
  readonly hostRef: ProductSummaryDetailsComponent = this;
  private readonly subscriptions = new Subscription();
  /**
   * The ID of the account for which to show the details
   * If not present, the widget has the `noAccountSelected` error state
   */
  readonly productId: Observable<string> = this.getRouteParam(this.route, 'selectedAccount').pipe(
    filter(productId => {
      const isMultiple = productId ? productId.split(',').length > 1 : false;
      this.multiple.next(isMultiple);
      return !isMultiple;
    }),
    map(productId => productId || ''),
  );
  /**
   * If the fetching of the account details failed, this property
   * holds the errors coming from the server
   */
  readonly error: Observable<AccountError | undefined> = this.service.error;
  /**
   * Defines the loading state during the loading of data.
   */
  readonly isLoading: Observable<boolean> = this.service.isLoading;
  /**
   * Event is triggered when an unmask button was clicked in any of the product types
   */
  private unmaskedProductEvent: BehaviorSubject<UnmaskedProductEventPayload | undefined> = new BehaviorSubject<
    UnmaskedProductEventPayload | undefined
  >(undefined);
  /**
   * List of maskable attributes to store their states
   */
  maskableAttributes: ProductMaskableAttribute[] = [];
  /**
   * The account details fetched from the server
   */
  readonly item: Observable<ProductType | undefined> = new Observable(subscriber => {
    const subscriptions: Subscription[] = [];
    subscriptions.push(
      this._item$.subscribe(product => {
        subscriber.next(product);

        if (product && hasExternalInfo(product)) {
          subscriptions.push(
            this.productsExternalInfoService
              .getProductsWithExternalInfo([product])
              .pipe(map(([productWithFinancialInstitution]) => productWithFinancialInstitution))
              .subscribe(subscriber),
          );
        }
      }, subscriber.error),
    );
    return () => subscriptions.forEach(subscription => subscription.unsubscribe());
  });

  private readonly _item$: Observable<ProductType | undefined> = combineLatest([
    this.service.productDetailsById.pipe(
      tap(() => {
        this.maskableAttributes = [];
        this.unmaskedProductEvent.next(undefined);
      }),
    ),
    this.unmaskedProductEvent.pipe(
      switchMap((payload: UnmaskedProductEventPayload | undefined) => {
        if (payload && !payload.attributeValue) {
          return this.getUnmaskedAttribute(payload).pipe(
            filter(unmaskedAttribute => !!unmaskedAttribute),
            tap((unmaskedAttribute: string) => {
              const maskableAttribute = this.maskableAttributes.find(
                attribute => attribute.attributeName === payload.attributeName,
              );
              if (maskableAttribute) {
                maskableAttribute.masked = false;
                maskableAttribute.unmaskedValue = unmaskedAttribute;
              }
            }),
            map(
              (unmaskedValue: string): UnmaskedProductEventPayload => ({
                attributeName: payload.attributeName,
                attributeValue: unmaskedValue,
                product: payload.product,
              }),
            ),
          );
        }
        return of(payload);
      }),
      map((payload: UnmaskedProductEventPayload | undefined) => {
        if (payload) {
          const { product, attributeName, attributeValue } = payload;
          return this.updateUnmaskedAttributes(product, attributeName, attributeValue || '');
        }
        return payload;
      }),
    ),
  ]).pipe(map(this.mapProductDetails.bind(this)));
  /**
   * Sets whether the bank customer can see details for multiple accounts at a time.
   */
  readonly multiple = new BehaviorSubject<boolean>(false);
  /**
   * List of supported product kind names
   */
  readonly productKindNames = ProductKindNames;
  /**
   * Joins the specified error messages to display on the template
   *
   * @param messages
   */
  processErrorMessages: (messages: string[] | undefined) => string | undefined = processErrorMessages;
  /**
   * Sets the passed unmasked attribute to the product
   *
   * @param product
   * @param attributeName
   * @param attributeValue
   * @private
   */
  private updateUnmaskedAttributes: (
    product: ProductType,
    attributeName: string,
    attributeValue: string,
  ) => ProductType = setAttribute;

  /**
   * Event handler for product kind details components like `bb-current-account-details`
   * Emits `UnmaskedProductEventPayload` to `unmaskedProductEvent`
   *
   * @param attributeName
   * @param product
   */
  onMaskAttribute(attributeName: MaskableAttribute, product: ProductType) {
    const maskableAttribute = this.maskableAttributes.find(
      attribute => attribute.attributeName === attributeName && attribute.maskedValue,
    );
    if (maskableAttribute) {
      this.unmaskedProductEvent.next({ attributeName, product, attributeValue: maskableAttribute.maskedValue });
      maskableAttribute.masked = true;
    }
  }

  /**
   * Event handler for product kind details components like `bb-current-account-details`
   * Emits `UnmaskedProductEventPayload` to `unmaskedProductEvent`
   *
   * @param attributeName
   * @param product
   */
  onUnmaskAttribute(attributeName: MaskableAttribute, product: ProductType) {
    const maskableAttribute = this.maskableAttributes.find(
      attribute => attribute.attributeName === attributeName && attribute.unmaskedValue,
    );
    const payload: UnmaskedProductEventPayload = { attributeName, product };
    if (maskableAttribute) {
      payload['attributeValue'] = maskableAttribute.unmaskedValue;
      maskableAttribute.masked = false;
    } else {
      this.maskableAttributes.push({
        attributeName,
        maskedValue: product[attributeName] as string,
      });
    }
    this.unmaskedProductEvent.next(payload);
  }

  /**
   * Constructor
   *
   * @param route
   * @param service
   * @param notificationService
   * @param localWindow
   * @param productsExternalInfoService
   */
  constructor(
    private readonly route: ActivatedRoute,
    private readonly service: AccountService,
    private readonly notificationService: NotificationService,
    @Inject(WINDOW) private readonly localWindow: LocalWindow,
    private readonly productsExternalInfoService: ProductsExternalInfoService,
  ) {}

  /**
   * Returns unmasked productItem if present otherwise masked one
   *
   * @param details
   * @private
   */
  private mapProductDetails(details: Array<ProductType | undefined>): ProductType | undefined {
    const [productDetails, unmaskedProduct] = details;
    return unmaskedProduct ? unmaskedProduct : productDetails;
  }

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }
    // ensure that level that has actual value keeps it from being cleared
    return combineLatest([paramValue, this.getRouteParam(route.parent, param)]).pipe(
      map(params => params[0] || params[1]),
    );
  }

  /**
   * @internal
   */
  ngOnInit() {
    this.service.loadProductDetailsFrom(this.productId);
  }
  /**
   * Prints out product details
   */
  print() {
    this.localWindow.print();
  }
  /**
   * @internal
   */
  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }
  /**
   * Fetches unmasked attribute for the given product
   * and returns a new product with the attribute unmasked
   */
  private getUnmaskedAttribute(unmaskedProductEventPayload: UnmaskedProductEventPayload): Observable<string> {
    const { attributeName, product } = unmaskedProductEventPayload;
    const requestParameters: UnmaskedAttributeRequestParams = {
      attributeName,
      arrangementId: product.id,
    };
    return this.service.getUnmaskedAttribute(requestParameters).pipe(
      catchError((error: AccountError) => {
        this.notificationService.showNotification({
          header: this.errorNotificationTemplate,
          message: this.processErrorMessages(error?.messages) as string,
          modifier: 'error',
        });
        return of('');
      }),
    );
  }
}
/**
 * This is an extension slot to be used for customizing the NoAccountSelected state
 *
 * @usageNotes
 *
 * ### Customize the NoAccountSelected view
 *
 * ```html
 * <ng-template
 *  bbProductSummaryDetailsNoAccountSelectedCustomizable
 *  [bbHostRef]="hostRef">
 *   Your custom template here
 * </ng-template>
 * ```
 *
 * @NgModule ProductSummaryDetailsWidgetModule
 */
@Directive({
  selector: '[bbProductSummaryDetailsNoAccountSelectedCustomizable]',
})
export class ProductSummaryDetailsNoAccountSelectedCustomizableDirective extends BbTemplate<
  ProductSummaryDetailsComponent,
  ProductType
> {}
