import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Observable, ReplaySubject, BehaviorSubject, of } from 'rxjs';
import { distinctUntilChanged, catchError, switchMap, filter, tap } from 'rxjs/operators';
import { AccountArrangementItem as ProductType, UnmaskedAttributeRequestParams } from '@backbase/data-ang/arrangements';
import { isEqual, ProductSummaryAccountsService } from '@backbase/product-summary-common-ang';
import { parseError, AccountError } from './account-error';

/**
 * Service for fetching and storing product summary account details.
 *
 * This service relies on providers from `ProductSummaryDetailsWidgetModule`.
 *
 * @see ProductSummaryDetailsWidgetModule
 *
 * @usageNotes
 *
 * ### Ensure the ProductSummaryDetailsWidgetModule is imported to your component module
 *
 * ```ts
 * @NgModule({
 *   ...
 *   imports: [
 *     ...
 *     ProductSummaryDetailsWidgetModule,
 *   ],
 * })
 * export class MyWidgetModule {}
 * ```
 *
 * ### Inject this service into your component
 *
 * ```ts
 * @Component({
 *   ...
 *   providers: [AccountService],
 * })
 *  export class MyComponent {
 * ```
 */
@Injectable()
export class AccountService {
  constructor(private readonly productSummaryAccountService: ProductSummaryAccountsService) {}

  private readonly arrangementId = new ReplaySubject<string>(1);
  /**
   * If the fetching of the account details failed, this property
   * holds the errors coming from the server
   */
  readonly error = new BehaviorSubject<AccountError | undefined>(undefined);
  /**
   * Defines the loading state during the loading of data.
   */
  readonly isLoading = new BehaviorSubject<boolean>(false);
  /**
   * Fetches from the server and returns product details for `arrangementId`
   */
  productDetailsById: Observable<ProductType | undefined> = this.arrangementId.pipe(
    distinctUntilChanged((a, b) => isEqual(a, b) && !this.error.value),
    filter(arrangementId => !!arrangementId),
    tap(() => this.error.next(undefined)),
    tap(() => this.isLoading.next(true)),
    switchMap((id: string) =>
      this.productSummaryAccountService.getAccountById(id).pipe(
        catchError((error: HttpErrorResponse) => {
          this.error.next(parseError(error));
          return of(undefined);
        }),
      ),
    ),
    tap(() => this.isLoading.next(false)),
  );
  /**
   * Subscribes on the passed `arrangementId`
   * Causes refetching data
   *
   * @param arrangementId
   */
  public loadProductDetailsFrom(arrangementId: Observable<string>) {
    arrangementId.subscribe(this.arrangementId);
  }

  /**
   * Fetches an unmasked attribute from the server for the given attribute name
   *
   * @param requestParameters
   */
  getUnmaskedAttribute(requestParameters: UnmaskedAttributeRequestParams): Observable<string> {
    return this.productSummaryAccountService.getUnmaskedAttribute(requestParameters);
  }
}
